import React, { useState,} from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  AccordionHeaderAnimated,
  AnimatedIcons,
  ArcContentAnimated,
  ListItemAnimated,
  UnorderedListAnimated,
} from "./AAComponents";
import { Accordion, HeaderTitle } from "./AccordionComponents";

function Index(props) {
  const [open, setOpen] = useState(false);
  const items = props.items;
  function alternate() {
    setOpen((val) => !val);
  }
  return (
    <Accordion>
      <AccordionHeaderAnimated className="header-arc"  onClick={alternate}>
        <HeaderTitle>{props.title}</HeaderTitle>
        <AnimatedIcons show={open}/>
      </AccordionHeaderAnimated>

      <AnimatePresence>
        {open && (
          <ArcContentAnimated>
            <UnorderedListAnimated>
              {props.faq && (
                <motion.div>
                  <ListItemAnimated>{items}</ListItemAnimated>
                </motion.div>
              )}
              {props.catalog &&
                items.map((item, index) => {
                  return (
                    <motion.div>
                      <ListItemAnimated index={index}>
                        {item.title}{" "}
                        <span className="amount">{item.amount}</span>
                      </ListItemAnimated>
                    </motion.div>
                  );
                })}
            </UnorderedListAnimated>
          </ArcContentAnimated>
        )}
      </AnimatePresence>
    </Accordion>
  );
}

export default Index;
