import React from "react";
import ServicesArc from "./index";
import { Catalog } from "../data/Catalog";
import { FaqList } from "../data/Faqs";
import styled from "styled-components";

function AccordionHolder() {
  return (
    <>
      <HolderWrapper>
        <ItemWrapper>
          <ServicesArc catalog data={Catalog} title="CATALOG" />
        </ItemWrapper>
        <ItemWrapper>
          <ServicesArc faq data={FaqList} title="FAQs" />
        </ItemWrapper>
      </HolderWrapper>
    </>
  );
}

export default AccordionHolder;

const ItemWrapper = styled.div`
  justify-content:center;
  align-items:center;
`;

const HolderWrapper = styled.div`
  /* width: 100vw; */
  display: flex;
  flex-direction: column;
`;
