import React from "react";
import {
  CustomerPhoto,
  CustomerPhotoGradient,
  Info,
  InfoWrapper,
  Title,
  TitleArea,
  TrendItemCard,
  WrapperTrending,
  Name,
  CustomerSocial,
  CardSection,
  Footer
} from "./TrendingComponents";

import wave3black from "../icons/wave3black.svg";

import { FaInstagram, } from "react-icons/fa";

function index(props) {
  const customers = props.data;
  return (
    <WrapperTrending>
      <TitleArea>
        <Title>Testimonials</Title>
      </TitleArea>
      <CardSection>
        {customers.map((data, index) => {
          return (
            <TrendItemCard key={index}>
              <CustomerPhoto src={data.img} alt={data.alt} />
              <CustomerPhotoGradient></CustomerPhotoGradient>

              <InfoWrapper>
                <Name>{data.name}</Name>
                <Info>
                  <span>"</span>
                  {data.info}
                  <span>"</span>
                </Info>
                <CustomerSocial>
                  <p>
                    <FaInstagram />
                  </p>
                </CustomerSocial>
              </InfoWrapper>
            </TrendItemCard>
          );
        })}
      </CardSection>
      {/* <Footer src={wave3black} /> */}
      {/* <Footer src={wave3black} style={{ transform: "rotateY(0deg)" }} /> */}
    </WrapperTrending>
  );
}

export default index;
