import { BlobText, KhlorisText, MediasText } from "./NHeaderComponents";

export const AnimatedBlobText = (props) => {
  return (
    <BlobText
      initial={{
        //   x:100,
        y: -1000,
        opacity: 0,
      }}
      animate={{
        y: 0,
        opacity: 1,
      }}
      transition={{
        type: "spring",
        stiffness: 120,
        duration: 3,
        repeat: Infinity,
        repeatDelay: 4,
        repeatType: "reverse",
      }}
    >
      {props.children}
    </BlobText>
  );
};

export const AnimatedKhlorisText = (props) => {
  return (
    <KhlorisText initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
      {props.children}
    </KhlorisText>
  );
};

export const AnimatedMediasText = (props) => {
  return (
    <MediasText
      initial={{ x: 100, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      transition={{
        type: "spring",
        stiffness: 120,
        duration: 3,
        repeat: Infinity,
        repeatDelay: 4,
        repeatType: "reverse",
      }}
    >
      {props.children}
    </MediasText>
  );
};
