import {
  AccordionHeader,
  ArcContent,
  HeaderIcon,
  ListItem,
  UnorderedList,
} from "./AccordionComponents";
import { AnimatePresence } from "framer-motion";
import {} from "react-icons";
import { FiPlus } from "react-icons/fi";
import { CgClose } from "react-icons/cg";

const arcVariants = {
  hidden: {
    height: 0,
  },
  visible: {
    height: "",
    // height:100,
    transition: {},
  },
  exits: {
    height: 0,
    transition: {
      type: "tween",
      when: "beforeChildren",
    },
    // opacity:0
  },
};

const ulVariants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.1,
    },
  },
  exit: {
    // scale:4
    when: "beforeChildren",
    staggerChildren: 0.2,
  },
};

const liItem = {
  hidden: {
    x: -50,
    opacity: 0,
  },
  visible: {
    x: 0,
    opacity: 1,
  },
  exit: {
    x: 100,
    opacity: 0,
    transition: {},
  },
};

const iconVariants = {
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
  exit: { opacity: 0 },
};

export const AccordionHeaderAnimated = (props) => {
  return (
    <AccordionHeader onClick={props.onClick}
    whileHover={{color:"gray"}}
    >
      {props.children}
    </AccordionHeader>
  )
}

export const ArcContentAnimated = (props) => {
  return (
    <ArcContent
      initial="hidden"
      animate="visible"
      variants={arcVariants}
      exit="exits"
    >
      {props.children}
    </ArcContent>
  );
};

export const AnimatedIcons = (props) => {
  return (
    <AnimatePresence>
      {props.show && (
        <HeaderIcon
          variants={iconVariants}
          initial="hidden"
          animate="visible"
          exit="exit"
        >
          <CgClose />
        </HeaderIcon>
      )}
      {!props.show && (
        <HeaderIcon
          variants={iconVariants}
          initial="hidden"
          animate="visible"
          exit="exit"
        >
          <FiPlus />
        </HeaderIcon>
      )}
    </AnimatePresence>
  );
};

export const UnorderedListAnimated = (props) => {
  return (
    <UnorderedList animate="visible" initial="hidden" variants={ulVariants}>
      {props.children}
    </UnorderedList>
  );
};

export const ListItemAnimated = (props) => {
  return (
    <ListItem key={props.index} variants={liItem}>
      {props.children}
    </ListItem>
  );
};
