import styled from "styled-components"; 
import background from  "../../images/background.jpg"
import {motion} from 'framer-motion'

export const Header = styled.div`
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;

  background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1)
    ),
    url(${background});
  background-size: cover;
  background-position: center;

  font-size: 20px;
  font-weight: 700;

  @media screen and (min-width: 400px) {
    /* height: 140px; */
    /* position: absolute; */
  }
`;

export const HeaderWrapper = styled.div`
  width: 100vw;
  height: 90vh;
  background-color: transparent;

  
`;

export const Content = styled.div`
  width: 100%;
  height: 100%;
  /* padding: 100px 20px; */

  display: flex;
  justify-content: center;
  align-items: center;
`;

export const NameUp = styled.div`
  width: 260px;
  display: flex;
  align-items: center;
  position: relative;

  @media screen and (min-width: 900px) {
    align-items: center;
    justify-content: center;
  }
`;

export const BlobText = styled(motion.div)`
  width: 200px;
  height: 200px;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  & img {
    width: 200px;
    transform: rotate(-20deg);
  }

`;

export const KhlorisText = styled(motion.p)`
  position: absolute;
  right: 46px;
  color: white;
`;

export const MediasText = styled(motion.div)`
  width: fit-content;
  color: #fff;
  top: 1px;
  position: relative;
  left: -40px;
`;

export const Footer = styled.img`
  width: 100vw;
  transform: rotateZ(180deg);
  filter: ${(props) => (props.shadow ? "drop-shadow(2px 2px 2px black)" : "")};
  background-color: ${(props) =>
    props.background ? "#a7a7a759" : "transparent"};

  @media screen and (min-width: 400px) {
    visibility: collapse;
  }
`;

export const IconBackground = styled.div`
  width: 100px;
  height: 100px;
  /* background-color:red; */

  position: absolute;
  top: ${(props) => props.top || ""};
  left: ${(props) => props.left || ""};
  right: ${(props) => props.right || ""};
  bottom: ${(props) => props.bottom || ""};

  z-index: -10;
`;

export const Icon = styled.p`
  /* color: rgba(0, 11, 118, 0.1); */
  color: rgba(82, 83, 97, 0.3);
  font-size: ${(props) => props.fontSize || "150px"};
  text-align: center;
  transform: ${(props) => `rotate(${props.rotate}deg)` || "50deg"};

  position: relative;
  /* z-index: -10; */
  /* transform: rotate(30deg); */
`;
