import React from "react";
import {
  HeaderWrapper,
  Content,
  NameUp,
  Footer,
  Header,
} from "./NHeaderComponents";
import {AnimatedBlobText, AnimatedKhlorisText, AnimatedMediasText} from "./HCAnimated"
import wave3black from "../icons/wave3black.svg";
import blob from "../../images//blob.svg";

// #000b76
// #0099ff

function index() {
  return (
    <Header className="header">
      <HeaderWrapper>
        <Content>
          <NameUp>
            <AnimatedBlobText>
              <img src={blob} />
              <AnimatedKhlorisText>
                Khloris
              </AnimatedKhlorisText>
            </AnimatedBlobText>
            <AnimatedMediasText>
              Medias
            </AnimatedMediasText>
          </NameUp>
        </Content>
      </HeaderWrapper>
      {/* <Footer src={wave3black} /> */}
      {/* <Footer src={wave3black} style={{ transform: "rotateY(0deg)" }} /> */}
    </Header>
  );
}

export default index;
