import styled from "styled-components";
import { FaChartArea } from "react-icons/fa";

export const WrapperTrending = styled.div`
  padding: 90px 0;
  width: 100%;
  margin: 0 auto;
  padding: 130px 20px 0px 20px;
  background-color: #a7a7a759;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

`;

export const TitleArea = styled.div`
  width: 100%;
  margin: 20px 0;
  color: rgba(0, 0, 0, 1);
`;

export const Title = styled.h3`
  display: inline-block;
  font-size: 1.7rem;
  font-weight: 900;
  padding-left: 4px;
  border-left: 4px solid black;
`;

export const TrendIcon = styled.p`
  font-size: 2rem;
  font-weight: 900;
`;

export const CardSection = styled.div`
  width: 100%;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
`;

export const TrendItemCard = styled.div`
  min-width: 300px;
  max-width: 300px;
  height: 600px;
  border-radius: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: #fff;
  overflow: hidden;
  position: relative;

  margin: 20px;

  /* border: 2px solid #ffeeee; */

  box-shadow: 0px 2px 6px 1px rgba(0, 0, 0, 0.4);
`;

export const CardIconWrapper = styled.div`
  border-radius: 300px;
  border: 1px solid rgba(0, 0, 0, 1);
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const CardIcon = styled.p`
  font-size: 1.7rem;
  font-weight: 900;
  color: rgba(0, 0, 0, 1);
`;

export const InfoWrapper = styled.div`
  width: 100%;
  height: 300px;
  overflow: hidden;
  padding: 20px 20px;
`;

export const Name = styled.p`
  /* font-style: italic; */
  color: #000b76;
  font-weight:600;
  margin-bottom: 4px;
`;

export const CustomerSocial = styled.div`
  width: 60px;
  height: 60px;
  margin: 0 auto;
  border-radius: 400px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.4);

  display: flex;
  justify-content: center;
  align-items: center;

  & p {
    margin-top: 3px;
    font-size: 1.6rem;
    text-align: center;
    color: #0099ff;
    color: #000b76;
  }
`;

export const Info = styled.p`
  /* padding:5px; */
  color: rgba(0, 0, 0, 0.7);

  & span {
    color: rgba(0, 0, 0, 0.3);
    font-size: 2rem;
    display: block;
  }
`;

export const CustomerPhoto = styled.img`
  width: 100%;
  height: 320px;
`;
export const CustomerPhotoGradient = styled.div`
  width: 100%;
  height: 320px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
  position: absolute;
  top: 0;
`;

export const Footer = styled.img`
  width: 100vw;
  transform: rotateZ(180deg);
  filter: ${(props) => (props.shadow ? "drop-shadow(2px 2px 2px black)" : "")};
  background-color: ${(props) =>
    props.background ? "#a7a7a759" : "transparent"};

  @media screen and (min-width: 400px) {
    visibility: collapse;
  }
`