import React from "react";
import Index from "./Accordion";

function ServicesInfo(props) {
  const headerStyle = {
    height: "55px",
    backgroundColor: "#000",
    display: "flex",
    alignItems: "center",
    padding: "0 20px",
    borderBottom: "1px solid white",
    color: "white",
  };
  return (
    <div>
      <div style={headerStyle}>
        <p>{props.title}</p>
      </div>
      {props.faq &&
        props.data.map((item, index) => {
          return (
            <Index key={index} title={item.question} items={item.answer} faq />
          );
        })}

      {props.catalog &&
        props.data.map((item, index) => {
          return (
            <Index key={index} title={item.title} items={item.itemsList} catalog />
          );
        })}
    </div>
  );
}

export default ServicesInfo;
