import { motion } from "framer-motion";
const { default: styled } = require("styled-components");

export const Accordion = styled(motion.div)`
  border-bottom: 1px solid black;
  background-color: black;
  color: white;
`;

export const AccordionHeader = styled(motion.div)`
  max-width: 100vw;
  box-sizing: border-box;
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: rgba(255, 255, 255, 1);
  font-size: 18px;
  padding: 10px 20px;
  border-bottom: 1px solid white;
`;

export const HeaderTitle = styled(motion.span)`
display: inline-block;
min-width: 200px;
overflow-wrap: break-word;
`;

export const HeaderIcon = styled(motion.span)`
  font-weight: 800;
  font-size:1.4rem
`;

export const ArcContent = styled(motion.div)`
  padding: 10px 20px;
  background-color: rgb(29, 27, 27);
`;

export const UnorderedList = styled(motion.ul)`
  background-color: rgba(255, 255, 255, 0.732);
`;

export const ListItemContainer = styled(motion.div)``;

export const ListItem = styled(motion.div)`
  padding: 4px 10px;
  border-bottom: 1px solid rgb(29, 27, 27);
  background-color: #fff;
  list-style: none;
  color: black;
`;
