const { default: styled } = require("styled-components");

export const Wrapper = styled.div`
  width: 100%;
  height: 520px;
  background-color: rgb(255 255 255 / 58%);

  padding: 20px 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-bottom: 4px solid black;
  position:relative;
`;

export const TextWrapper = styled.div`
  width: 300px;
  height: 300px;
`;

export const Text = styled.p`
  font-size: 2.8rem;
  font-weight: 700;
  text-align: center;
  color: #000b76;
`;

export const Button = styled.button`
  min-width: 200px;
  max-width: 220px;
  background-color: transparent;
  padding: 15px 23px;
  border: 2px solid #d9d9d1d6;
  font-size: 1.2rem;
  font-weight: 800;
  color: #0707077d;

  & span {
    color: #000b76b5;
  }
`;

export const Footer = styled.img`
  position: absolute;
  top: ${(props) => props.top || ""};
  bottom: ${(props) => props.bottom || ""};
  width: 100vw;
  transform: rotateZ(180deg);
  filter: ${(props) => (props.shadow ? "drop-shadow(2px 2px 2px black)" : "")};
  background-color: ${(props) =>
    props.background ? "#a7a7a759" : "transparent"};

  @media screen and (min-width: 400px) {
    visibility: collapse;
  }
`;
