import React from "react";
import {
  CompanyName,
  FooterWrapper,
  IconWrapper,
  SocialWrapper,
} from "./FooterComponents";
import { FaFacebook, FaTwitter, FaWhatsapp, FaInstagram } from "react-icons/fa";

function index() {
  return (
    <FooterWrapper>
      {/* <FooterBg src={footer} /> */}
      <CompanyName>Khloris Medias</CompanyName>
      <SocialWrapper>
        <IconWrapper>
          <FaTwitter />
        </IconWrapper>
        <IconWrapper>
          <FaWhatsapp />
        </IconWrapper>
        <IconWrapper>
          <FaFacebook />
        </IconWrapper>
        <IconWrapper>
          <FaInstagram />
        </IconWrapper>
      </SocialWrapper>
    </FooterWrapper>
  );
}

export default index;
